.footer {
	font-family: var(--menu-font);
	font-weight: 400;
	font-size: var(--menu-font-size);
	color: var(--menu-color);
	box-shadow: 0 0 8px rgba(0, 0, 0, 0.5);
	z-index: 1;
	@media (max-width: 360px){
		font-size: 16px;
		.footer__container-mail {
			left: 20px;
			svg {
				width: 15px;
				height: 15px;
				left: -21px;
			}
		}
		.footer__container-phone svg {
			width: 15px;
			height: 15px;
			left: -17px;
}

	}

	&__container {
		// padding: 0 20px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: var(--footer-height);
	}

	&__container-copyright {
		position: relative;
		right: -30px;
		svg {
			position: absolute;
			top: 1px;
			left: -25px;
			width: 20px;
			height: 20px;
			fill: var(--menu-color);
		}
		@media (max-width: 560px){
			display: none;
		}
	}

