.about {
	position: relative;
	&__image {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 250px;
		padding: 0 15px;
		z-index: -1;
		@media (max-width: 767px){
			position: relative;
			margin-top: 20px;
			height: 180px;
			padding: 0;
		}
		&:before {
			content: "";
			position: absolute;
			left: 0;
			right: 0;
			bottom: 0;
			top: 0;
			// z-index: 1;
			// background: rgb(255, 255, 255);
			// opacity: .4;
			background: linear-gradient(to right, rgba(252, 252, 252, 1) 0%, rgba(252, 252, 252, 1) 40%, rgba(252, 252, 252, 0) 90%);
			transition: all .9s ease 0s;
			@media (max-width: 767px){
				background: none;
			}
		}
	img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}
.section {
		&__body {
			z-index: 1;
			width: 50%;
			height: 250px;
			@media (max-width: 767px){
				width: 100%;
				height: auto;
				.section__description p {
					font-size: 16px!important;
			}
			}
		}
}
