.gallery {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-template-rows: repeat(10, minmax(70px,1fr));
	grid-gap: 5px;
	@media (max-width: 767px){
		display: block;
	}

		&__item {
			position: relative;
			display: flex;
			padding: 20px;
			align-items: flex-end;
			overflow: hidden;
			transition: all 0.6s ease 0s;
			@media (max-width: 767px){
				height: 150px;
				margin-bottom: 10px;
				padding: 0;
			}
			&:hover {
				.all__proj {
					transition: all 0.8s ease 0s;
					text-shadow: 0 0 10px rgb(130, 130, 130),0 0 10px rgb(130, 130, 130),0 0 20px rgb(130, 130, 130),0 0 30px rgb(130, 130, 130);
					padding-left: 15px;
					@media (max-width: 767px){
						text-shadow: none;
						padding-left: 0;
					}
				}
				h3 {
					text-shadow: 0 0 20px rgb(130, 130, 130),0 0 20px rgb(130, 130, 130),0 0 30px rgb(130, 130, 130),0 0 40px rgb(130, 130, 130);
					@media (max-width: 767px){
						text-shadow: none;
					}
				}
			.gallery__item-holder img {
					transition: all 2s ease 0s;
					transform: scale(1.05);
					@media (max-width: 767px){
						transform: scale(1);
					}
				}
				.gallery__item-holder::before {
					transition: all 2s ease 0s;
					opacity: 0;
				}
			}

			h3 {
				font-family: var(--menu-font);
				text-transform: uppercase;
				color: var(--color-black);
				padding: 0;
				margin: 0;
				@media (max-width: 767px){
					width: 100%;
					background-color: rgba(255, 255, 255, 0.5);
					text-align: center;
					padding: 5px 0;
				}
			}
		}

		&__item-holder {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			overflow: hidden;
			z-index: -1;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				transition: all 0.9s ease 0s;
			}
			&:before {
				content: "";
				position: absolute;
				left: 0;
				right: 0;
				bottom: 0;
				top: 0;
				z-index: 1;
				background: rgb(255, 255, 255);
				opacity: .4;
				transition: all .9s ease 0s;
				@media (max-width: 767px){
					opacity: 0;
				}
			}
		}
}

.item-1 {
	grid-column: 1/2;
	grid-row: 1/5;
}
.item-2 {
	grid-column: 1/2;
	grid-row: 5/11;
}
.item-3 {
	grid-column: 2/3;
	grid-row: 1/7;
}
.item-4 {
	grid-column: 2/3;
	grid-row: 7/11;
}
.item-5 {
	grid-column: 3/4;
	grid-row: 1/4;
}
.item-6 {
	grid-column: 3/4;
	grid-row: 4/9;
}
.item-7 {
	grid-column: 3/4;
	grid-row: 9/11;
	align-items: center;
	justify-content: center;
	h3 {
		transition: all 0.3s ease 0s;
		position: relative;
		span {
			position: absolute;
		}
	}
	@media (max-width: 767px){
		align-items: flex-end;
	}
}
