// * импорт глобальных стилей
@import "vars";
@import "fonts";
@import "vendor";
@import "settings";

.phone {
	position: relative;
	padding: 0 20px;
	transition: all 0.3s ease 0s;
	&:hover {
		transition: all 0.3s ease 0s;
		text-shadow: 0 0 10px rgb(130, 130, 130),0 0 10px rgb(130, 130, 130),0 0 20px rgb(130, 130, 130),0 0 30px rgb(130, 130, 130);
	}
	svg {
		position: absolute;
		top: 1px;
		left: -5px;
		width: 20px;
		height: 20px;
		fill: var(--menu-color);
	}
}

.mail {
	position: relative;
	right: -30px;
	transition: all 0.3s ease 0s;
	&:hover {
		transition: all 0.3s ease 0s;
		text-shadow: 0 0 10px rgb(130, 130, 130),0 0 10px rgb(130, 130, 130),0 0 20px rgb(130, 130, 130),0 0 30px rgb(130, 130, 130);
	}
	svg {
		position: absolute;
		top: 0px;
		left: -27px;
		width: 20px;
		height: 20px;
		fill: var(--menu-color);
	}
}
