.news {
	
		&__container {
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			padding-bottom: 5px;
			@media (max-width: 767px){
				flex-wrap: wrap;
			}

		}

		&__item {
			display: flex;
			flex-direction: column;
			flex-wrap: nowrap;
			justify-content: space-between;
			width: 30%;
			@media (max-width: 767px){
				width: 100%;
			}
			&:hover {
				.news__item-holder img {
					transition: all 1s ease 0s;
					filter: none;
				}
			}

		}

		&__date {
			position: relative;
			&:before {
				content: "";
				position: absolute;
				top: 10px;
				left: 80px;
				background-color: #888;
				width: calc(100% - 80px);
				height: 1px;
			}
		}

		&__item-holder {
			padding-top: 20px;
			width: 100%;
			height: 200px;
			img {
				filter: grayscale(100%);
				width: 100%;
				height: 100%;
				object-fit: cover;
				transition: all 1s ease 0s;
				@media (max-width: 767px){
					filter: none;
				}
		}
		}

		&__all {
			transition: all 0.3s ease 0s;
			position: relative;
			text-transform: uppercase;
			transform: translate(-50%, 0px);
			margin-left: 50%;
			display: inline-block;
			text-align: center;
			@media (max-width: 767px){
				text-shadow: 0 0 10px rgb(130, 130, 130),0 0 10px rgb(130, 130, 130),0 0 20px rgb(130, 130, 130),0 0 30px rgb(130, 130, 130);
			}
			&:hover {
				transition: all 0.3s ease 0s;
				text-shadow: 0 0 10px rgb(130, 130, 130),0 0 10px rgb(130, 130, 130),0 0 20px rgb(130, 130, 130),0 0 30px rgb(130, 130, 130);
				padding-left: 15px;
				@media (max-width: 767px){
					padding-left: 0;
				}
			}
			span {
				position: absolute;
				right: -28px;
				top: -3px;
			}
			}
}
