html.dark {
	body {
		color: var(--color-white);
		background-color: var(--color-bg-dark);
	}

	.header {
		color: var(--menu-color-dark);
		transition: all 0.3s ease 0s;
		&._scroll {
			transition: all 0.5s ease 0s;
			background-color: var(--color-header-dark);
			box-shadow: 0 0 8px rgba(255, 255, 255, 0.5);
			}
	
		&__phone svg {
			fill: var(--menu-color-dark);
		}
		&__logo svg .fil2, svg .fil1 {
			fill: var(--menu-color-dark);
		}
		.menu {
			&__body {
				@media (max-width: 767px){
					&::before {
						background-color: var(--color-header-dark);
					}
					background-color: rgba(0, 0, 0, 0.9);
				}
			}
			&__icon {
				@media (max-width: 767px){
					& span,
					&::before,
					&::after {
						background-color: var(--menu-color-dark);
					}
				}
			}
		}
	}

	.footer {
		color: var(--menu-color-dark);
		background-color: var(--color-header-dark);
		box-shadow: 0 0 8px rgba(255, 255, 255, 0.5);
		svg {
			fill: var(--menu-color-dark);
		}
	}

	.container {
		.section__title {
			color: var(--color-white);
		}
	}
	.intro { 
	&__content {
		color: var(--color-white);
	}

	&__swiper {
		.swiper-wrapper {
			&:before {
				background-color: rgba(0, 0, 0, 0.7);
			}
		}
		}
	}

	.info {
	&__slider-wraper {
		&:before {
			background-color: rgba(0, 0, 0, 0.4);
		}
		}
	}

	.gallery {
		&__item {
			h3 {
				color: var(--color-white);
				@media (max-width: 767px){
					background-color: rgba(0, 0, 0, 0.5);
				}
			}
		}

		&__item-holder {
			&:before {
				background: rgb(0, 0, 0);
				opacity: .4;
				transition: all .9s ease 0s;
				@media (max-width: 767px){
					opacity: 0;
				}
			}
		}
	}

	.about__image { 
		&::before{
			background: linear-gradient(to right, rgba(8, 8, 8, 1) 0%, rgba(8, 8, 8, 1) 40%, rgba(8, 8, 8, 0) 100%);
			@media (max-width: 767px){
				background: none;
			}
		}
		img {
			@media (max-width: 767px){
				filter: invert(100%);
			}
		}
	}

	.contacts__body {
		color: #aaa;
		.contacts__worktime {
			color: var(--color-white);
		}
		svg {
			fill: var(--menu-color-dark);
		}
		.form__title {
			color: var(--color-white);
		}
		input[type=radio]:checked + label {
			color: var(--color-white);
		}
		input[type=radio]:checked + label:before {
			border: 1px solid var(--color-white);
		}
		input[type=radio]:checked + label:after {
			border: 4px solid var(--color-white);
			display: inline-block;
		}
		.form__input {
			color: var(--color-white);
			border-bottom: 1px solid #aaa;
		}
		.form__button {
			color: var(--color-white);
			span {
				position: absolute;
				right: -28px;
				top: -3px;
			}

		}

	}
}