.header {
	z-index: 10;
	font-family: var(--menu-font);
	font-weight: 400;
	font-size: var(--menu-font-size);
	color: var(--menu-color);
	transition: all 0.3s ease 0s;

	&._scroll {
		transition: all 0.5s ease 0s;
		background-color: var(--color-header);
		opacity: .95;
		box-shadow: 0 0 8px rgba(0, 0, 0, 0.5);
	}
	&__container {
		margin: 0 auto;
		display: flex;
		// padding: 0 20px;
		align-items: center;
		justify-content: space-between;
		min-height: var(--header-height);
	}

	&__logo {
		position: relative;
		z-index: 5;
		cursor: pointer;
		svg {
			width: 70px;
			height: 50px;
		}
		svg .fil2, svg .fil1 {
			fill: var(---menu-color);
		}
	}
}

.menu {
	&__icon {
		@media (max-width: 767px){
			z-index: 5;
			display: block;
			position: relative;
			width: 30px;
			height: 18px;
			cursor: pointer;

			& span,
			&::before,
			&::after {
				left: 0;
				position: absolute;
				height: 10%;
				width: 100%;
				transition: all 0.3s ease 0s;
				background-color: var(--menu-color);
			}
			&::before,
			&::after {
				content: "";
			}
			&::before {
				top: 0;
			}
			&::after {
				bottom: 0;
			}
			& span {
				top: 50%;
				transform: scale(1) translate(0px, -50%);
			}
			&._active {
				span {
					transform: scale(0) translate(0px, -50%);
				}
				&::before {
					top: 48%;
					transform: rotate(-45deg) translate(0px, -50%);
				}
				&::after {
					bottom: 48%;
					transform: rotate(45deg) translate(0px, 50%);
				}
			}
		}
	}

	&__block {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 0 0 0 40px;
	// .header__phone {
	// 	position: relative;
	// 	padding: 0 20px;
	// 	transition: all 0.3s ease 0s;
	// 	&:hover {
	// 		transition: all 0.3s ease 0s;
	// 		text-shadow: 0 0 10px rgb(130, 130, 130),0 0 10px rgb(130, 130, 130),0 0 20px rgb(130, 130, 130),0 0 30px rgb(130, 130, 130);
	// 	}
	// 	svg {
	// 		position: absolute;
	// 		top: 1px;
	// 		left: -5px;
	// 		width: 20px;
	// 		height: 20px;
	// 		fill: var(--menu-color);
	// 	}
	// }
	.themetoggle {
		padding: 0 0 20px 0;
		cursor: pointer;
		height: 22px;
		&:hover {
			transition: all 0.3s ease 0s;
			text-shadow: 0 0 10px rgb(130, 130, 130),0 0 10px rgb(130, 130, 130),0 0 20px rgb(130, 130, 130),0 0 30px rgb(130, 130, 130);
		}
	}

	@media (max-width: 767px){
			order: -1;	
			padding: 30px 0 40px 0;
			width: 100%;
			.header__phone {
				font-size: 24px;
				padding: 0 0 0 35px;
				order: -2;
				align-self: center;
				&:hover {
					text-shadow: none;
				}
						svg {
					left: 0;
					top: 0px;
					width: 26px;
					height: 27px;
				}
			}
			.themetoggle {
				height: 40px;
				&:hover {
					text-shadow: none;
				}
				span {
					font-size: 40px;
				}
			}
		
	}
	}
	
	&__body {
		display: flex;
		align-items: center;
		&__list {
			padding-right: 20px;
		}
		@media (max-width: 767px){
			position: fixed;
			top: 0;
			left: -100%;
			width: 100%;
			height: 100%;
			background-color: rgba(255, 255, 255, 0.9);
			padding: 70px 30px 30px 30px;
			transition: left 0.3s ease 0s;
			overflow: auto;
			flex-direction: column;
			justify-content: start;
			align-items: start;
			&::before {
				content: "";
				position: fixed;
				width: 100%;
				top: 0;
				left: 0;
				height: var(--header-height);
				background-color: var(--color-header);
				box-shadow: 0 0 8px rgba(0, 0, 0, 0.5);
				z-index: 2;
			}
			&._active {
				left: 0;
			}
			.menu {
				&__list > li {
					flex-wrap: wrap;
					margin: 0 0 30px 0;
				}
				&__list > li._active {
					.menu__sub-list {
						display: block;
					}
				}
				&__list > li:last-child {
					margin-bottom: 0;
				}
				&__link {
					font-size: 24px;
				}
				&__sub-list {
					display: none;
					position: relative;
					background-color: #fff;
					flex: 1 1 100%;
					margin: 20px 0 0 0;
				}
				&__sub-link {
					font-size: 20px;
				}
			}
		}
	}

	&__list {
		margin: 0;
		& > li {
			position: relative;
			margin: 0 0 0 20px;
		}
		@media (min-width: 767px){
			display: flex;
			align-items: center;
			& > li {
				padding: 10px 0;
			}
		}
	}

	&__link {
		font-size: 18px;
		text-transform: uppercase;
		transition: all 0.3s ease 0s;
		&:hover {
			transition: all 0.3s ease 0s;
			text-shadow: 0 0 10px rgb(130, 130, 130),0 0 10px rgb(130, 130, 130),0 0 20px rgb(130, 130, 130),0 0 30px rgb(130, 130, 130);
		}
	}

	&__sub-list {
		position: absolute;
		top: 100%;
		right: 0;
		background-color: #000;
		padding: 15px;
		min-width: 200px;
		& li:not(:last-child) {
			padding: 0 0 10px 0;
		}

		@media (min-width: 767px){
			transform: translate(0px, 10px);
			opacity: 0;
			visibility: hidden;
			pointer-events: none;
			transition: all 0.3s ease 0s;
		}
	}

	&__sub-link {
		color: var(--menu-color);
		&:hover {
			text-decoration: underline;
		}
	}
}

body._pc {
	.menu__list > li:hover {
		.menu__sub-list {
			opacity: 1;
			visibility: visible;
			transform: translate(0px, 0px);
			pointer-events: all;
		}
	}
}

body._touch {
	.menu__arrow {
		display: block;
		width: 0;
		height: 0;
		margin: 0 0 0 8px;
		transition: transform 0.3s ease 0s;
		border-left: 5px solid transparent;
		border-right: 5px solid transparent;
		border-top: 10px solid #fff;
	}
	.menu__link {
		flex: 1 1 auto;
	}
	.menu__list > li._active {
		.menu__sub-list {
			opacity: 1;
			visibility: visible;
			transform: translate(0px, 0px);
			pointer-events: all;
		}
		.menu__arrow {
			transform: rotate(-180deg);
		}

	}
	.menu__list > li {
		display: flex;
		align-items: center;
	}
}