.container {
}
.contacts {
	color: #555;
	
		&__body {
			display: flex;
			justify-content: space-between;
		}

		&__info {
			width: 40%;
			display: flex;
			flex-direction: column;
			flex-wrap: nowrap;
			align-content: flex-start;
			justify-content: space-around;
			align-items: flex-start;
		}

		&__address {
			margin: 20px 0 10px 0;
		}

		&__phone {
			margin-bottom: 10px;
			svg {
				left: 0;
				width: 16px;
				height: 16px;
				top: 3px;
			} 
		}

		&__mail {
			margin-bottom: 15px;
			right: 0;
			padding: 0 20px;
			svg {
				left: 0;
				width: 16px;
				height: 16px;
				top: 2px;
			} 
		}

		&__worktime {
			margin-top: 20px;
			color: var(--color-black);
		}

		&__form {
			width: 40%;
		}
}
.section {

		&__title {
			color: var(--color-black);
		}
}
.phone {
}
.mail {
}
.social {

		&__icons {
			width: 100%;
			margin: 15px 0 20px 0;
			display: flex;
			justify-content: space-around;
		}
}
.si svg {
	width: 35px;
	height: 35px;
	transition: all .3s ease 0s;
	&:hover{
		transition: all .3s ease 0s;
		filter: drop-shadow(0 0 10px rgb(130, 130, 130));
	}

}
.form {

		&__body {
			.radio__buttons {
				margin-bottom: 30px;
				input[type=radio] {
					display: none;
				}
				label {
					display: inline-block;
					cursor: pointer;
					position: relative;
					padding: 0 15px 0 22px;
					margin-right: 0;
					user-select: none;
					transition: all 0.3s ease 0s;
					&:before {
						content: "";
						display: inline-block;
						width: 18px;
						height: 18px;
						position: absolute;
						left: 0;
						top: 1px;
						border-radius: 50%;
						border: 1px solid #555;
					}
					&:after {
						content: "";
						display: none;
						width: 8px;
						height: 8px;
						position: absolute;
						left: 5px;
						top: 6px;
						border-radius: 50%;
						border: 4px solid #555;
					}
					&:hover {

						transition: all 0.3s ease 0s;
						text-shadow: 0 0 10px rgb(130, 130, 130),0 0 10px rgb(130, 130, 130),0 0 20px rgb(130, 130, 130),0 0 30px rgb(130, 130, 130);
					}

				}
				input[type=radio]:checked + label {
					color: var(--color-black);
				}
				input[type=radio]:checked + label:before {
					border: 1px solid var(--color-black);
				}
				input[type=radio]:checked + label:after {
					border: 4px solid var(--color-black);
					display: inline-block;
				}

			}
		}

		&__title {
			color: var(--color-black);
			padding-bottom: 20px;
		}

		&__item {
			position: relative;
			margin-bottom: 20px;
		}


		&__button {
			border:none;
			padding: 0;
			background-color: rgba(0,0,0,0);
			transition: all 0.3s ease 0s;
			position: relative;
			text-transform: uppercase;
			display: inline-block;
			text-align: center;
			cursor: pointer;
			@media (max-width: 767px){
				text-shadow: 0 0 10px rgb(130, 130, 130),0 0 10px rgb(130, 130, 130),0 0 20px rgb(130, 130, 130),0 0 30px rgb(130, 130, 130);
			}
			&:hover {
				transition: all 0.3s ease 0s;
				text-shadow: 0 0 10px rgb(130, 130, 130),0 0 10px rgb(130, 130, 130),0 0 20px rgb(130, 130, 130),0 0 30px rgb(130, 130, 130);
				padding-left: 15px;
				@media (max-width: 767px){
					padding-left: 0;
				}
			}
			span {
				position: absolute;
				right: -28px;
				top: -3px;
			}

		}
}

.link {
		&__back {
			position: relative;
			margin-bottom: 30px;
		}
}

.form__label {
	position: absolute;
	top: 0px;
	left: 0px;
	transition: all .9s ease 0s;
}
.form__input {
	border: none;
	border-bottom: 1px solid #555;
	background-color: rgb(0, 0, 0, 0);
	width: 100%;
	padding: 0 0 0 35px;
	transition: all .6s ease 0s;
	&:focus {
		transition: all .6s ease 0s;
		outline:none;
		padding: 0;
	}
	&:hover{
		transition: all .6s ease 0s;
		padding: 0!important;
	}
	&:hover + label {
		transition: all .6s ease 0s;
		top: -20px;
		text-shadow: 0 0 10px rgb(130, 130, 130),0 0 10px rgb(130, 130, 130),0 0 20px rgb(130, 130, 130),0 0 30px rgb(130, 130, 130);
				}
	&:focus + label {
		transition: all .6s ease 0s;
		text-shadow: 0 0 10px rgb(130, 130, 130),0 0 10px rgb(130, 130, 130),0 0 20px rgb(130, 130, 130),0 0 30px rgb(130, 130, 130);
		top: -20px;
	}
}
