.intro {
	position: relative;

	&__swiper {
		width: 100%;
		height: 100vh;
	&-wrapper {
		&:before {
			content: "";
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			z-index: 2;
			background-color: rgba(255, 255, 255, 0.4);
		}
	}
	}

	&__content {
		position: absolute;
		display: flex;
		flex-direction: column;
    justify-content: center;
		z-index: 1;
		// padding-left: 10%;
		color: var(--color-black);
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		h1 {
			font-size: 3em;
		}
		h2 {
			font-size: 2em;
		}
		@media (max-width: 767px){
			h1 {
				font-size: 2em;
			}
			h2 {
				font-size: 1.5em;
			}
		}
		@media (max-width: 560px){
			text-align: center;
			padding-left: 0;
			h1 {
				font-size: 1.5em;
			}
			h2 {
				font-size: 1em;
			}
		}
	}
}

.swiper-slide {
	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}

.info {
	position: relative;

	&__text {
		position: absolute;
		top: 0;
		left: 0;
		width: 40%;
		height: 100%;
		overflow: hidden;
		z-index: 2;
		@media (max-width: 767px){
			position: relative;
			width: 100%;
			p {
				font-size: 16px!important;
			}
		}
	}

	&__slider-wraper {
		&:before {
			content: "";
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			z-index: 2;
			background-color: rgba(255, 255, 255, 0.4);
		}
		}
}
